import { computed } from 'vue';
export default {
    props: {
        value: { type: Number, required: true },
        color: { type: String, required: true },
    },
    setup: function (props) {
        var colors = computed(function () {
            return props.color
                ? "bg-" + props.color + "-700 text-" + props.color + "-100"
                : null;
        });
        return { colors: colors };
    },
};
