import { computed } from 'vue';
export default {
    props: {
        value: { type: Number, required: true },
        color: { type: String, required: true },
    },
    setup: function (props) {
        var content = computed(function () {
            return props.value !== 0 ? String(props.value) : null;
        });
        var colors = computed(function () {
            return props.color
                ? "bg-" + props.color + "-200 text-" + props.color + "-700"
                : null;
        });
        return { content: content, colors: colors };
    },
};
